@import 'ScssHelpers/helpers-all.scss';

.blog__marquee_w {
	overflow: hidden;
	background-color: $white;
	padding: 1.25rem 0;
}

.blog__marquee {
	display: flex;
	min-width: 100%;

	flex-wrap: nowrap;
	will-change: transform;
}

.blog__marquee_content {
	@include flex_block(row, nowrap, flex-start, center, center);

	position: relative;

	font-family: $inter;
	font-weight: 700;
	font-size: 2.4rem;
	font-style: italic;
	color: $black;

	white-space: nowrap;
	text-transform: uppercase;
	line-height: 1;

	p {
		padding: 0 0.8rem;
	}
}
